.navbar {
    min-height: 80px;
  } 
  
  .navbar-brand {
    padding: 0 15px;
    height: 60px;
    line-height: 60px;
  }
  
  .navbar-toggle {
    /* (80px - button height 34px) / 2 = 23px */
    margin-top: 23px;
    padding: 9px 10px !important;
  }
  
  @media (min-width: 768px) {
    .navbar-nav > li > a {
      /* (80px - line-height of 27px) / 2 = 26.5px */
      padding-top: 26.5px;
      padding-bottom: 26.5px;
      line-height: 27px;
    }
  }
 
  .alignment1-home{ 
      font-size: 32px;
  }

  .alignment2-home{
      font-size: 20px;
  }
 
  .navlink-home{
       color: gray;
  }

  .navbrand-home{
     
    color: black;
    font-family: Arial, Helvetica, sans-serif;
  }